<template>
  <div id="app">
    <span>
      <v-card
        class="mt-2 mx-auto"
        max-width="500"
        v-for="(article, index) in articles"
        :key="index"
      >
        <img
          :src="article.urlToImage"
          class="white--text align-end"
          width="100%"
          himgight="150px"
        />
        <v-card-title class="white--text">{{ article.title }}</v-card-title>
        <img />

        <v-card-subtitle class="pb-0">Number 10</v-card-subtitle>

        <v-card-text class="text--primary">
          <div>{{ article.title }}</div>

          <div>Whitsunday Island, Whitsunday Islands</div>
        </v-card-text>

        <v-card-actions>
          <v-btn color="orange" text>
            Share
          </v-btn>

          <v-btn color="orange" text>
            Explore
          </v-btn>
        </v-card-actions>
      </v-card>
    </span>
  </div>
</template>

<script>
import axios from "axios";
export default {
  name: "app",
  data() {
    return {
      articles: []
    };
  },

  // Fetches articles when the component is created.
  created() {
    axios
      .get(
        "https://newsapi.org/v2/everything?q=bitcoin&from=2019-12-07&sortBy=publishedAt&apiKey=09590bf5df894de8a925ceed73fab34a"
      )
      .then(response => {
        // JSON responses are automatically parsed.
        this.articles = response.data.articles;
      });

    // async / await version (created() becomes async created())
    //
    // try {
    //   const response = await axios.get(`http://jsonplaceholder.typicode.com/posts`)
    //   this.posts = response.data
    // } catch (e) {
    //   this.errors.push(e)
    // }
  }
};
</script>
